body {
  background-color: rgba(255, 253, 192, 0.569);
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}
.container {
  width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
